      title.setAttribute("id", `battery-empty-${BatteryEmptyDirective.titleIdCounter}`);
      svg.appendChild(title);
      svg.setAttribute("aria-labelledby", `battery-empty-${BatteryEmptyDirective.titleIdCounter}`);
